.c-intro {
  position: relative;
  width: 100%;
  height: 100%;

  &__bg {
    background-image: url("../../assets/bgs/bgintro.jpg");
    position: fixed;
    top: 0;
    width: 100%;
    height: 108rem;
    z-index: -1;
  }
  &__head {
    padding-top: 23.4rem;
  }
  &__link {
    position: relative;
    color: black;
    text-decoration: none;
    display: inline-flex;
    justify-content: flex-start;
    transform: rotate(90deg);
    align-items: center;
    margin-left: -5rem;
    margin-top: 9.1rem;

    &::before {
      content: "";
      width: 0;
      height: 0.1rem;
      position: absolute;
      bottom: 0;
      background: black;
      transition: 0.3s width ease-out;
    }

    &:hover {
      &::before {
        width: calc(100% - 3rem);
        transition: 0.3s width ease-in;
      }
    }

    &-arrow {
      display: inline-block;
      background-image: url("../../assets/icons/arrow.svg");
      width: 2rem;
      height: 3rem;
      transform: rotate(-90deg);
      margin-left: 2rem;
    }
  }
}
