.c-heading {
  margin: 0;
  &--l1 {
    font-size: 2.7rem;

    @include respond-to("md") {
      font-size: 5.7rem;
    }
  }

  &--l2 {
    font-size: 2rem;

    @include respond-to("md") {
      font-size: 3rem;
    }
  }

  &--l4 {
    font-size: 1.6rem;
  }

  &--l5 {
    font-size: 1.2rem;
  }

  &--link {
    font-size: 2rem;
  }

  &--light {
    font-weight: 100;
  }

  &--regular {
    font-weight: normal;
  }

  &--semibold {
    font-weight: 600;
  }

  &--bold {
    font-weight: bold;
  }

  &--italic {
    font-style: italic;
  }
}
