.c-nav {
  position: relative;
  z-index: 1000;
  top: 0;

  &__logo {
    position: fixed;
    left: 1rem;
    top: 2rem;
    width: 15rem;
    height: 15rem;
    background-image: url("../../assets/logo.svg");

    @include respond-to("md") {
      left: 4.5rem;
      top: 1rem;
    }
  }

  &__menu {
    position: fixed;
    top: 18rem;
    right: 0;
    color: black;
    display: none;

    @include respond-to("sm") {
      display: block;
    }

    &-list {
      opacity: 0.5;
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-bottom: 3.8rem;

      .c-heading--link {
        color: white;
        text-decoration: none;
      }

      &::after {
        content: "";
        display: inline-block;
        width: 4rem;
        height: 0.2rem;
        background: white;
        margin-left: 1.6rem;
      }

      &.is-intoview {
        opacity: 1;

        .c-heading--link {
          position: relative;
          &::after {
            content: "";
            width: 30%;
            height: 0.2rem;
            display: block;
            position: absolute;
            bottom: 0;
            left: 0;
            background: #ffb400;
          }
        }

        &::after {
          width: 8rem;
        }
      }
    }
  }
}
