.c-contact {
  position: relative;
  background-image: url("../../assets/bgs/bgcontact.png");
  background-position: top center !important;
  margin-top: -30rem;
  width: 100%;
  height: auto;
  z-index: 0;

  &__text {
    padding: 30rem 0 10rem 0;
    color: white;
    max-width: 100rem;
  }

  &__social {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    @include respond-to("sm"){
      justify-content: flex-start;
    }
  }

  &__network {
    width: 15rem;
    height: 15rem;
    margin: 0.5rem;
    display: block;
    transition: 0.7s all ease-out;

    @include respond-to('sm') {
      margin: 2rem;
    }

    &:hover {
      transform: rotate(360deg);
      transition: 0.7s all ease-in;
    }
  }
}
