.c-briefcase {
  position: relative;
  background-image: url("../../assets/bgs/bgBriefcase.jpg");
  width: 100%;
  height: 118rem;
  z-index: 0;
  padding: 10rem 0;

  &__text {
    padding-top: 15rem;
    max-width: 100rem;
  }

  &__slide {
    .swiper-container {
      width: 100%;
      height: 42rem;
    }
  }

  &__card {
    width: 100%;
    height: 100%;
    position: relative;

    &-dark-layer {
      position: absolute;
      top: 0;
      background-image: linear-gradient(to right, black, rgba(53, 53, 53, 0.363));
      width: 100%;
      height: 100%;
      z-index: 0;
    }

    &-content {
      z-index: 10;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      color: white;
      padding: 4rem;
    }

    &-title {
      position: relative;
      margin-bottom: 5rem;

      @include respond-to(sm) {
        margin-bottom: 10rem;
      }
      &::before {
        content: "";
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;
        height: 0.3rem;
        width: 30%;
        background: #ffb400;
      }
    }

    &-button {
      padding: 1rem 2rem;
      background-color: #ffb400;
      border-radius: 0.3rem;
      color: white;
      text-decoration: none;
      margin-top: 3rem;
      display: block;
      transition: 0.3s all ease-out;

      @include respond-to(sm) {
        margin-top: 4rem;
      }

      &:hover {
        background-color: #ffb300ad;
        transition: 0.3s all ease-in;
      }
    }
  }
}
