.c-about {
  z-index: 10;
  margin-top: 11rem;
  height: auto;
  width: 100%;
  background-image: url("../../assets/shapes/black-shape.png");
  background-position: top left !important;

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 22rem 0 20rem 0;

    @include respond-to("md") {
      flex-direction: row;
    }
  }

  &__text {
    color: white;
    max-width: 69.8rem;
    padding-top: 20rem;
  }

  &__map {
    margin-left: 0;
    padding-top: 5rem;

    @include respond-to("md") {
      margin-left: 5%;
      padding-top: 20rem;
    }

    .leaflet-container {
      width: 30rem;
      height: 30rem;
      border-radius: 50%;

      @include respond-to("md") {
        width: 40rem;
        height: 40rem;
      }
    }
  }
}
